.OutputContainer {
    display: block;
    width: 100%;
    text-align: left;
}

.OutputSpan {
    margin: 0.5rem 0;
    padding: 0 0.1rem;
    font-weight: 600;
    font-size: 1.2rem;
    display: block;
}

.OutputTextArea {
    width: 100%;
    min-width: 100%;
    max-width: 100%;
    min-height: 30vh;
    font-family: 'Roboto', sans-serif;
    background-image: linear-gradient(180deg, #fdfbfb 0%, #ebedee 100%);
    border-radius: 0.5rem;
    height: 10rem;
    display: block;
    padding: 0.5rem;
}

.OutputTextArea::-webkit-scrollbar {
    width: 12px;
    cursor: pointer;               /* width of the entire scrollbar */
}
  
.OutputTextArea::-webkit-scrollbar-track {
    background-image: linear-gradient(180deg, #fdfbfb 0%, #fcfcfc 100%);
    border-radius: 20px; 
    cursor: pointer;
}
  
.OutputTextArea::-webkit-scrollbar-thumb {
    background-color: #bdbdbd;    /* color of the scroll thumb */
    border-radius: 20px;       /* roundness of the scroll thumb */
    border: 3px solid #fdfbfb;  /* creates padding around scroll thumb */
    cursor: pointer;
}