.App {
  text-align: center;
  width: 90%;
  max-width: 770px;
  margin: auto;
  margin-top: 5.5rem;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

label[for='custom']{
  position: absolute;
  left: 1.5rem;
  top: auto;
  transform: translate(0,2rem);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.required{
  color: red;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
