*, *::after, *::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

h1,h2,h3,h4,h5 {
  padding: 0.5rem;
}

body {
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  padding-bottom: 15rem;
  background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);

}

body::-webkit-scrollbar {
  width: 18px;               /* width of the entire scrollbar */
}

body::-webkit-scrollbar-track {
  background-image: linear-gradient(180deg, #fdfbfb 0%, #fcfcfc 100%);
  border-radius: 20px; 
}

body::-webkit-scrollbar-thumb {
  background-color: #bdbdbd;    /* color of the scroll thumb */
  border-radius: 20px;       /* roundness of the scroll thumb */
  border: 4.5px solid #fdfbfb;  /* creates padding around scroll thumb */
  cursor: pointer;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
